.skills{
    width: 100%;
    padding: 6rem,1rem;
    background-color: #000;
}
.card-conatiner{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 40px;
    padding-top: 5rem;
}

.card{
    border: 1px solid #eee;
    color: #eee;
    text-align: center;
    padding: 1rem;
}
.card:hover{
    background-color: #756872;
    box-shadow: 0px 3px 12px 0px #756872;
    border: 1px solid transparent;
}

.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}
.card p{
    padding: 10px 0;
}

.bar{
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;

}
.btc{
    font-size: 2rem;
    font-weight: 600;
    padding: 1rem 0;
}
.btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}

@media screen and (max-width:740px) {
    .card-conatiner{
        max-width: 1140px;
        margin: auto;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;
    }
}